/** @format */
import { DropDownButton } from "@progress/kendo-react-buttons";
import { Popup } from "@progress/kendo-react-popup";
import { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { UserLogout } from "../../Creators/UserCreator";
import Apis, { endpoints } from "../../config/Apis";
import cookies from "react-cookies";
import { useNavigate } from "react-router-dom";
import DropDownList from "../DropDown/DropDownList";
export default function ButtonAvatar() {
  const navigate = useNavigate();
  const anchor = useRef(null);
  const [show, setShow] = useState(false);
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user.user);
  const image = user !== null && user !== undefined ? user.avatar_path : "";
  const items = [
    { action: () => profileHandle(), displayName: "Profile", icon: "k-i-info" },
    { action: () => logoutHandle(), displayName: "Logout", icon: "k-i-logout" },
    {
      action: () => logoutHandle(),
      displayName: "Settings",
      icon: "k-i-settings",
    },
  ];

  const onClick = () => {
    setShow(!show);
  };
  const logoutHandle = async () => {
    try {
      await Apis.get(endpoints["logout"], {
        headers: {
          Authorization: `Bearer ${cookies.load("access-token")}`,
        },
      });
      dispatch(UserLogout());
      navigate("/");
    } catch (err) {
      console.log(err);
      dispatch(UserLogout());
      navigate("/");
    }
  };
  const profileHandle = () => {
    navigate(`/profile/${user.username}/${user.id}/`);
  };
  return (
    <div
      className="SiteAvatar TopNavigationItem RightNavigationItem"
      onClick={onClick}
    >
      <div>
        <div id="react-aria6806590017-26" className="Avatar" ref={anchor}>
          <img
            alt="nh h s"
            className="AssemblyAvatar"
            src={image}
            style={{
              backgroundImage: `url(${image})`,
              height: "40px",
              width: "40px",
            }}
          />
          {show ? (
            <DropDownList show={show} items={items} marginLeft="-150px" />
          ) : (
            ""
          )}
        </div>
      </div>
    </div>
  );
}
